"use client";

import { useEffect, useState } from "react";
import { db } from "@/lib/firebase";
import { collection, getDocs } from "firebase/firestore";

export default function AdminDashboard() {
  const [usersData, setUsersData] = useState([]);
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const fetchStressData = async () => {
      try {
        const snapshot = await getDocs(collection(db, "stressLevels"));
        const data = snapshot.docs.map(doc => ({
          uid: doc.id,
          stressLevel: doc.data().stressLevel,
          updatedAt: doc.data().updatedAt?.toDate ? doc.data().updatedAt.toDate() : doc.data().updatedAt
        }));

        setUsersData(data);
      } catch (err) {
        console.error("Error fetching stress data:", err);
      } finally {
        setLoading(false);
      }
    };

    fetchStressData();
  }, []);

  return (
    <div className="min-h-screen p-8 bg-gray-100">
      <h1 className="text-3xl font-bold mb-6">Admin Dashboard - Student Stress Levels</h1>

      {loading ? (
        <p>Loading...</p>
      ) : (
        <div className="overflow-x-auto">
          <table className="min-w-full bg-white shadow-md rounded-lg overflow-hidden">
            <thead className="bg-indigo-600 text-white">
              <tr>
                <th className="py-3 px-6 text-left">Student UID</th>
                <th className="py-3 px-6 text-left">Stress Level</th>
                <th className="py-3 px-6 text-left">Last Updated</th>
              </tr>
            </thead>
            <tbody>
              {usersData.map((user) => (
                <tr key={user.uid} className="border-b hover:bg-gray-50">
                  <td className="py-3 px-6">{user.uid}</td>
                  <td className="py-3 px-6">{user.stressLevel} / 10</td>
                  <td className="py-3 px-6">{user.updatedAt?.toLocaleString()}</td>
                </tr>
              ))}
            </tbody>
          </table>
        </div>
      )}
    </div>
  );
}
